* {
    font-family: "Gilroy Light";
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
}

#root {
    width: 100%;
    height: 100vh;
    background: #333;
    user-select: none;
    padding: 100px 0;
    overflow-x: hidden;
}

.ham {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 80;
    width: 100%;
    padding: 10px 20px;
    display: flex;
    align-items: center;
}

.icon {
    cursor: pointer;
    display: inline-block;
}

div.close.closeSideBar {
    font-size: 1.2rem;
}

.modalCont {
    width: 100%;
    height: 100%;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background: rgba(0, 0, 0, 0.4);
}

.modal {
    z-index: 101;
    width: clamp(300px, 50%, 600px);
    max-height: 95vh;
    position: fixed;
    background-color: #333;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.5), 0px 12px 20px rgba(0, 0, 0, 0.3);
}

div.modalCont.openModal {
    display: block;
}
div.modalCont.closeModal {
    display: none;
}

div.modalCont.rename.openModal {
    display: block;
}
div.modalCont.rename.closeModal {
    display: none;
}

.modal h3 {
    font: size 1.3rem;
}
.modal .input-container {
    margin: 20px auto;
}

.modal input {
    color: white;
    background-color: #333;
    box-shadow: inset 0px 3px 5px rgba(0, 0, 0, 0.5) !important;
}

.modal .setName,
.canceldelete {
    float: right;
    padding: 10px;
    color: white;
    background: #444;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
}

.modal .setName:active,
.canceldelete:active {
    box-shadow: inset 0px 3px 5px rgba(0, 0, 0, 0.5);
}

.noList {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: wrap column;
    justify-content: center;
    align-items: center;
    text-align: center;
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
}

.dir {
    margin-bottom: -3px;
}

.closeBtn {
    padding: 0;
}

.sideBar {
    position: absolute;
    width: clamp(280px, 70%, 500px);
    top: 0;
    left: 0;
    background: #222;
    z-index: 99;
    height: 100vh;
    transition: all 0.3s ease-out;
    overflow-y: scroll;
}

.sideBar::-webkit-scrollbar {
    width: 5px;
    background: rgba(0, 0, 0, 0.1);
}
.sideBar::-webkit-scrollbar-thumb {
    width: 5px;
    background: rgba(0, 0, 0, 0.3);
}
.sideBar.closeBar {
    transform: translateX(-100%);
    box-shadow: none;
}
.sideBar.openBar {
    transform: translateX(0);
    box-shadow: 10px 0px 20px rgba(0, 0, 0, 0.5);
    user-select: select auto;
}

.sideBar.lightMode {
    background: white;
}

.sideBar h2 {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 55px;
    letter-spacing: 2px;
    font-size: 1rem;
    background: rgba(0, 0, 0, 0.5);
}

.listSet,
.subList {
    color: white;
    width: 100%;
    cursor: pointer;
    display: grid;
    grid-template-columns: 1.5rem auto 1.5rem 1.5rem;
    column-gap: 10px;
    align-items: center;
    padding: 1rem;
    transition: 0.3s;
}

.listSet.light {
    background: white;
}

.listSet.light h3 {
    color: #222;
}

.subList {
    grid-template-columns: 1.5rem auto;
}

.subList.light h3 {
    color: #333;
}

hr {
    display: block;
    margin: 10px auto;
    height: 1px;
    border: none;
    background-color: rgba(255, 255, 255, 0.5);
}

.listSet.active,
.listSet:active,
.subList:active {
    background: rgba(0, 0, 0, 0.2);
}

.listSet.light.active,
.listSet.light:active,
.subList.lightMode:active {
    background: #d6d6d6;
}

.icons {
    display: flex;
    align-items: center;
}

.listSet .tag {
    display: flex;
    align-items: center;
}

.modal p {
    font-size: 12px;
    margin: 10px 0;
}
.modal button:nth-child(1) {
    margin-top: 20px;
}
.modal button {
    display: block !important;
    margin: 10px auto;
    float: none !important;
}

.copy {
    padding: 5px 10px;
    margin-top: 20px;
    color: white;
    background: #444;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
}

.canceldelete {
    padding: 0 !important;
}
.donateModal .info-block {
    margin: 20px auto;
}
.donateModal h4 {
    font-size: 14px;
}

.donateModal p,
.donateModal pre {
    font-size: 10px;
}

.container {
    width: 600px;
    margin: auto;
    position: relative;
    padding: 30px;
    border-radius: 10px;
}

.ListName {
    margin: 20px 0;
}

.control {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
}

.box {
    display: grid;
    grid-template-columns: 90px;
    grid-template-rows: 90px;
    position: relative;
}

.box .percent {
    position: relative;
    width: 100%;
    height: 100%;
}

.box svg {
    width: 100%;
    height: 100%;
}

.box circle.circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke-width: 10px;
    stroke-dasharray: 250.92137145996094;
    stroke-dashoffset: 250.92137145996094;
    stroke-linecap: round;
    transition: stroke-dashoffset 0.5s ease, stroke-width 0.5s ease;
}
.box svg circle.dark {
    stroke: rgba(0, 0, 0, 0.5);
    stroke-dasharray: 0;
    stroke-dashoffset: 0;
}

.box svg circle + .grad {
    stroke: url("#grad");
}

.box .number {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 18px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.number span.perc {
    font-size: 12px;
}

.number h2 {
    font-size: 22px;
}

.number.LightMode h2,
.number.LightMode span {
    color: #333;
}

span.comp {
    font-size: 9px;
    font-weight: 400;
    transform: translateY(-5px);
}
h2.empty {
    font-size: 12px;
}

.buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 90px;
}

.buttons button,
.buttons select,
.modal button {
    display: inline-block;
    border: none;
    outline: none;
    width: 115px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 500;
    font-size: 12px;
    padding: 5px;
    letter-spacing: 1px;
    transition: 0.5s ease;
}

button.disable {
    background: rgb(77, 77, 77);
    box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.5);
}

button.clear {
    background: #d32f2f;
    box-shadow: 0px 3px 8px #3f0101;
}

select {
    background: #222;
    color: white;
    background: rgb(77, 77, 77);
    box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.5);
}

.inputBlock {
    display: grid;
    place-items: center;
    grid-template-columns: auto 45px;
    column-gap: 3%;
    height: 50px;
    margin: 15px 0;
}

.input-container {
    width: 100%;
    height: 45px;
    position: relative;
    border-radius: 10px;
}

input[type="text"] {
    padding: 12px 15px;
    font-size: 16px;
    display: inline-block;
    color: #333;
    border-radius: 10px;
    outline: none;
    border: none;
    width: calc(100% - 5.5px);
    height: calc(100% - 5px);
    margin: 2.5px 0 0 2.5px;
    z-index: 15;
    position: relative;
    transition: 0.5s ease;
    width: 100%;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
}

input:focus,
input:valid {
    color: white;
    background-color: #333;
}

.input {
    width: calc(100% + 5px);
    position: absolute;
    overflow: hidden;
    border-radius: 8px;
    height: 45px;
    top: 0%;
    left: 0%;
}

.input::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0%;
    left: 0%;
    transform: translate(0%, -101%);
    border-radius: 8px;
    width: 100%;
    height: 100%;
    background: #6544ff;
    z-index: 10;
    transition: 0.5s ease;
}

input:focus + div::before,
input:valid + div::before {
    transform: translate(0%, 0%);
}

.plus {
    background: white;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    border: none;
    outline: none;
    display: grid;
    place-items: center;
    cursor: pointer;
    font-size: 18px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
}

.listContainer {
    background: #222;
    border-radius: 10px;
    padding: 0px 10px;
    min-height: 45px;
    display: flex;
    flex-direction: column-reverse;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
}

.listContainer.LightMode {
    background: #d6d6d6;
    color: #333;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
}

div.label {
    height: 100%;
    display: flex;
    min-height: 45px;
    align-items: center;
    border-bottom: 1px solid #555;
    animation: slide 0.5s ease-out;
    -webkit-animation: slide 0.5s ease-out;
}

label {
    flex: 70%;
    display: inline-block;
    cursor: pointer;
    padding: 20px 10px;
    padding-left: 0;
}

input[type="checkbox"] {
    display: none;
}

@keyframes slide {
    from {
        opacity: 0;
        transform: translateY(-50%);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

div.label:first-child {
    border: none;
}

.checkbox-container {
    height: 30px;
}
.checkbox {
    stroke: white;
    stroke-width: 8;
    fill: transparent;
    width: 30px;
}

.listContainer.LightMode .checkbox {
    stroke: #333;
}

.checkbox path {
    stroke-dasharray: 324.0476989746094;
    stroke-dashoffset: 0;
    transition: 0.4s;
}

.check {
    fill: none;
    stroke-dasharray: 79.19596099853516;
    stroke-dashoffset: 79.19596099853516;
    transition: 0.4s;
}

input[type="checkbox"]:checked + div svg.checkbox path {
    stroke-dashoffset: 324.0476989746094;
}
input[type="checkbox"]:checked + div svg.checkbox .check {
    stroke-dashoffset: 0;
    fill: none;
}

.Todo {
    display: grid;
    grid-template-columns: 30px auto;
    align-items: center;
    column-gap: 5%;
    flex: 80%;
}

.Todo > span {
    font-size: 16px;
    position: relative;
    word-break: break-all;
}

.listContainer.LightMode .Todo > span {
    color: #333;
}

input[type="checkbox"]:checked + div div div span:nth-child(1) {
    height: 12px;
}

input[type="checkbox"]:checked + div div div span:nth-child(2) {
    width: 22px;
}

.close {
    color: red;
    font-size: 20px;
    text-shadow: 0px 0px 5px rgba(255, 0, 0, 0.6);
    display: inline-grid;
    height: 30px;
    width: 30px;
    place-items: center;
    cursor: pointer;
    transition: transform 0.2s ease-out;
}

@media only screen and (max-width: 900px) {
    .container {
        width: 80%;
    }
}
@media only screen and (max-width: 700px) {
    .box {
        transform: scale(0.7) translateY(-15%);
        transform-origin: left 50%;
    }

    .control {
        height: 70px;
        align-items: unset;
    }

    .buttons {
        height: 70px;
        justify-content: space-between;
    }

    .buttons button,
    .buttons select {
        height: 30px;
        width: 100px;
        font-size: 9px;
    }
}

@media only screen and (max-width: 500px) {
    #root {
        padding: 0;
    }
    .container {
        padding: 70px 20px;
        border-radius: 10px;
        width: 100%;
        min-height: 100vh;
        border-radius: 0px;
    }

    button.plus {
        width: 35px;
        height: 35px;
        font-size: 12px;
    }

    .inputBlock,
    .input-container {
        height: 40px;
    }

    .listContainer {
        min-height: 40px;
    }

    .inputBlock {
        margin: 10px 0;
    }

    .input {
        height: 40px;
    }

    .inputBox {
        padding: 5px 10px;
        font-size: 12px;
        height: 35px;
        border-radius: 5px;
    }

    .checkbox-container {
        height: 20px;
    }

    .checkbox {
        width: 20px;
        height: 20px;
    }

    label {
        padding: 15px;
        padding-left: 0;
    }

    .Todo {
        grid-template-columns: 20px auto;
        min-height: 20px;
    }

    div.label .Todo > span {
        font-size: 12px;
    }

    div.close {
        width: 30px;
        height: 30px;
        font-size: 14px;
    }
}
